body {
    color: $primeryTextColor;
    background-color: #ffffff;
    font-family: "Poppins", sans-serif;
}

html {
    box-sizing: border-box;
}

*,
*::before,
*::after {
    box-sizing: inherit;
}

a {
    text-decoration: none;
    color: rgba(70, 70, 70, 1);
}

ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

img {
    display: block;
    height: auto;
    width: 100%;
}
